<script>
	import Vue from 'vue'
	import store from './store'
    import {getByKey} from './util/auth'
    import TOKEN_MESSAGE_CONTANTS from './constant/commonConstant'

    export default {
		onLaunch: function() {
			uni.getSystemInfo({
				success: function(e) {
					// #ifndef MP
					Vue.prototype.StatusBar = e.statusBarHeight;
					if (e.platform == 'android') {
						Vue.prototype.CustomBar = e.statusBarHeight + 50;
					} else {
						Vue.prototype.CustomBar = e.statusBarHeight + 45;
					};
					// #endif

					// #ifdef MP-WEIXIN
					Vue.prototype.StatusBar = e.statusBarHeight;
					let custom = wx.getMenuButtonBoundingClientRect();
					Vue.prototype.Custom = custom;
					Vue.prototype.CustomBar = custom.bottom + custom.top - e.statusBarHeight;
					// #endif

					// #ifdef MP-ALIPAY
					Vue.prototype.StatusBar = e.statusBarHeight;
					Vue.prototype.CustomBar = e.statusBarHeight + e.titleBarHeight;
					// #endif
				}
			})

			Vue.prototype.ColorList = [
			    {
					title: '嫣红',
					name: 'red',
					color: '#e54d42'
				},
				{
					title: '桔橙',
					name: 'orange',
					color: '#f37b1d'
				},
				{
					title: '明黄',
					name: 'yellow',
					color: '#fbbd08'
				},
				{
					title: '橄榄',
					name: 'olive',
					color: '#8dc63f'
				},
				{
					title: '森绿',
					name: 'green',
					color: '#39b54a'
				},
				{
					title: '天青',
					name: 'cyan',
					color: '#1cbbb4'
				},
				{
					title: '海蓝',
					name: 'blue',
					color: '#0081ff'
				},
				{
					title: '姹紫',
					name: 'purple',
					color: '#6739b6'
				},
				{
					title: '木槿',
					name: 'mauve',
					color: '#9c26b0'
				},
				{
					title: '桃粉',
					name: 'pink',
					color: '#e03997'
				},
				{
					title: '棕褐',
					name: 'brown',
					color: '#a5673f'
				},
				{
					title: '玄灰',
					name: 'grey',
					color: '#8799a3'
				},
				{
					title: '草灰',
					name: 'gray',
					color: '#aaaaaa'
				},
				{
					title: '墨黑',
					name: 'black',
					color: '#333333'
				},
				{
					title: '雅白',
					name: 'white',
					color: '#ffffff'
				},
			]

		},
		data() {
			return {
				networkState: ''
			}
		},
		onShow: function() {
			// uni.showToast({
			//     title:"onShow加载，开始缓存判断------------",
			//     position: 'bottom',
			//     icon: 'none',
			//     duration:3000
			// })
			var _this = this
            let templateType = getByKey(TOKEN_MESSAGE_CONTANTS.PATROL_TEMPLATE_TYPE)
            // 1、判断缓存中有没有templateType数据，没有就请求，如果请求不到跳转错误页面，
            // 2、进行缓存，如果无法链接网络，那么跳转至作错误页面
            if(templateType==null || templateType==''){
				// uni.showToast({
				//     title:"缓存中有没有templateType数据,进行vuex判断------------",
				//     position: 'bottom',
				//     icon: 'none',
				//     duration:3000
				// })
                var patrol = store.getters.patrolTemplateType
                if(patrol==undefined || patrol.length==0){
					// uni.showToast({
					//     title:"vuex中有没有templateType数据,进行请求------------",
					//     position: 'bottom',
					//     icon: 'none',
					//     duration:3000
					// })
                    _this.$store.dispatch('GetPatrolTemplateType').then(res=>{

                    }).catch(error=>{
                        uni.navigateTo({
                            url: 'error?id=1&errMsg=无法连接网络,且没有缓存数据，请链接网络后重试'
                        });
                    })
                }else{
                    console.debug(store.getters.patrolTemplateType)
                }
            // 缓存中有分类信息，但vuex中没有
            }else if(store.getters.patrolTemplateType.length==0 ){
				// uni.showToast({
				//     title:"缓存中有分类信息，但vuex中没有，缓存templateType到vuex中------------",
				//     position: 'bottom',
				//     icon: 'none',
				//     duration:3000
				// })
                _this.$store.dispatch('setPatrolTemplateTypeFromCache',templateType)
            }

            // 2、检查缓存中是否有模板信息内容
            let patrolTemplate = getByKey(TOKEN_MESSAGE_CONTANTS.PATROL_TEMPLATE)
            if(patrolTemplate.length==0){
				// uni.showToast({
				//     title:"缓存中没有模板信息内容，请求模版信息------------",
				//     position: 'bottom',
				//     icon: 'none',
				//     duration:3000
				// })
                _this.$store.dispatch('GetPatrolTemplate').then(res=>{
                }).catch(error=>{
                    uni.navigateTo({
                        url: 'error?id=1&errMsg=无法加载巡检模板数据，请链接网络后重试'
                    });
                })
                // 向vuex中设置内容
            }else if(store.getters.patrolTemplate.length==0){
				// uni.showToast({
				//     title:"缓存中有模版信息但vuex中没有，缓存模版信息到vuex中------------",
				//     position: 'bottom',
				//     icon: 'none',
				//     duration:3000
				// })
                _this.$store.dispatch('setPatrolTemplateFromCache',patrolTemplate)
            }
            // 判断用户是否登录了
            let token = getByKey(TOKEN_MESSAGE_CONTANTS.ACCESS_TOKEN_NAME);
            if(token==null || token===''){
                uni.redirectTo({
                    url: './pages/electric/index'
                });
            }else{
                uni.redirectTo({
                    url: './pages/electric/inspectionIndex'
                });
            }
			
		},
		onHide: function() {

		},
		methods:{

		}

	}
</script>

